<template>
  <div class="withdrawalRecords">
    <div class="withdrawalRecordsHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="$router.push('/merchantCenter')" />
        <span>{{ $t("index.text84") }}</span>
      </div>
    </div>
    <div class="slot"></div>

    <div class="withdrawalRecordsMain">
      <div
        class="rewardItem jcsb"
        v-for="(item, index) in recordsList"
        :key="index"
      >
        <div class="left">
          <div class="reward">+1000USDT</div>
          <div class="reward1">-10000HF</div>
          <div class="tiem">2024-12-12 14:45</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      recordsList: []
    };
  },
  activated() {
    this.onLoad();
  },
  methods: {
    async selling(item) {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text21"),
        className: "loading"
      });
      try {
        let gasPrice = await this.getGasPrice();
        let gas = await this.core
          .coreContract()
          .sell(this.toWei(item.hfNum), item.id)
          .estimateGas();

        this.referrals = await this.core
          .coreContract()
          .sell(this.toWei(item.hfNum), item.id)
          .send({
            from: this.userAddress,
            gas: gas + 10000,
            gasPrice: gasPrice
          });
        this.$toast(this.$t("index.text154"));
        this.getPurchaseRecords();
      } catch (error) {
        this.$toast.clear();
        console.log(error);
      }
    },

    async onLoad() {
      let res = await this.$api.get("transactionlog/index", {
        page: 1,
        limit: 1000000,
        type: 5
      });
      this.recordsList = res.data.list;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.withdrawalRecords {
  height: 100%;
  overflow: scroll;
  background: #ffffff;
}
.withdrawalRecordsHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}

.withdrawalRecordsMain {
  padding: 0 2rem 4rem 2rem;

  .rewardItem {
    height: 12rem;
    border-bottom: 0.1rem solid #d9d9d9;
    padding: 1.6rem 0;
    .left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;

      .reward {
        font-weight: bold;
        font-size: 1.8rem;
      }
      .reward1 {
        font-weight: bold;
        color: #d12c21;
        font-size: 1.6rem;
      }
      .tiem {
        color: #666666;
      }
    }
  }
}
</style>



